import { Pipe, PipeTransform } from '@angular/core';
import { HttpClient,HttpRequest,HttpResponse} from '@angular/common/http'

@Pipe({name: 'linkpreview'})
export class LinkPreviewPipe implements PipeTransform {
   constructor(private http: HttpClient) {


      
    }
  private extractData(res: Response) {
    let body:any=res;
      return `<app-link-preview> </app-link-preview>`;

  }
  transform(value: string, args: string[]): any {
   return `
<section class="main-section">
    <div class="one">   <img class="img-responsive" src="http://3.bp.blogspot.com/-IbEOTNtCMyU/TfCAdHaAxEI/AAAAAAAAA8U/EATib38SSAM/s320/joe-mcelderry.jpg" alt="...">
</div>
   
    <div class="two">
       <h4>Facebook </h4>
      <p>
        asdfasfddsafasfd
      </p>
      </div>
</section>`;
    // if (!value) return value;
    //     return value.split(' ').map(function (s) { return s.charAt(0); }).join('');
  }
}


