import { Component, ElementRef, ViewChild, OnInit, OnChanges, Input, Inject, ViewEncapsulation } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HubConnection } from '@aspnet/signalr-client';
import { UserService, HubService } from '../shared/services'
import { Message, User, Conversation, SendingModel } from '../shared/model'
import { HttpClient, HttpRequest, HttpEventType, HttpResponse } from '@angular/common/http'
import { APP_CONFIG, AppConfig } from "../shared/app.config";
import { Image } from 'angular-modal-gallery';

@Component({
    selector: 'app-messages-component',
    templateUrl: './messages.component.html',
    styleUrls: ["./messages.component.css"]
})

export class MessagesComponent implements OnInit {
    userScrolling: boolean;
    sending: string[] = [];
    @ViewChild('messagelist') mList: ElementRef;
    private _conversation: Conversation;
    get conversation(): Conversation {
        return this._conversation;
    }
    @Input()
    set conversation(conv: Conversation) {
        this._conversation = conv;
        this.parseConversationData();
    }
    messages: (Message | SendingModel)[];
    homeUser: User;
    awayUser: User;
    userName: string;
    message: string;

    constructor(private hubService: HubService, private http: HttpClient, @Inject(APP_CONFIG) private config: AppConfig) {
     

    }
    isMessage(message: any) {

        if (message.userId) return true;
        return false;
    }
    isSending(message) {
        if (message.userId) return false;
        return true;
    }
    uploadPercentageClass(percentage: string) {
        return `p${percentage}`;
    }
    upload(files:FileList) {
                console.log(files);

        if(files.length==0) return;
        let filenames=Array.from(files).map(file => file.name).join(", ");
        var sModel = new SendingModel();
        sModel.fileName = filenames;
        this.messages.push(sModel);
        console.log(this.sending);
        if (files.length === 0)
            return;

        const formData = new FormData();

        for (var i=0;i<files.length;i++)
            formData.append(files[i].name, files[i]);

        const req = new HttpRequest('POST', `${this.config.apiEndpoint}/chat/files/${this.conversation.id}/${this.homeUser.id}`, formData, {
            reportProgress: true,
        });

        let uploadRequest = this.http.request(req).subscribe(
            event => {
            if (event.type === HttpEventType.UploadProgress) {
            let uploadProgress = Math.round(100 * event.loaded / event.total);
                sModel.progress = uploadProgress.toString();
            }
            else  if (event.type === HttpEventType.Response) {
              
                 let index=  this.messages.indexOf(sModel);
               this.messages.splice(index,1);
            }
        },
        err=>{sModel.failed=true;console.log(sModel.failed);});
        sModel.cancelRequest = () => {
            console.log("cancel request called");
           
            uploadRequest.unsubscribe();
               let index=  this.messages.indexOf(sModel);
               this.messages.splice(index,1);
        };
    }
    typing(isTyping: boolean) {
        this.hubService.invoke('TypingStatus', this.conversation.id, this.homeUser.id, isTyping);
    }

    scrolling(event: any) {

        //           let height=event.srcElement.scrollHeight;
        //         let scroll=event.srcElement.scrollTop +event.srcElement.offsetHeight;
        //         if(height<=scroll +5 && height>=scroll-5)
        //         {
        //  this.userScrolling=true;
        //         }
        //         else{
        //              this.userScrolling=false;
        //         }


    }

    scrollToBottom() {
        try {
            //           let height=event.srcElement.scrollHeight;
            //         let scroll=event.srcElement.scrollTop +event.srcElement.offsetHeight;
            //         if(height<=scroll +5 && height>=scroll-5)
            //         {
            //  this.userScrolling=true;
            //         }
            //         else{
            //              this.userScrolling=false;
            //         }


            //   let height=   this.mList.nativeElement.scrollHeight;
            //   let scroll=this.mList.nativeElement.scrollTop +this.mList.nativeElement.offsetHeight;
            //   console.log(scroll);
            //   console.log(height);
            //        if(scroll +10<height)
            // {
            //     console.log("should not ");
            //       this.mList.nativeElement.scrollTop = this.mList.nativeElement.scrollHeight ;

            // }
            // else{
            // }


        } catch (err) {

            console.log(err);
        }
    }
    public isMe(message: Message): Boolean {
        return message.userId == this.homeUser.id;
    }
    public sendMessage(): void {
        if (!this.message) return;
        this.hubService.invoke('SendMessage', this.message, this.conversation.id, this.homeUser.id);
        this.message = "";
    }
    onEnter() {
        this.sendMessage();
    }

    ngOnInit() {
        this.parseConversationData();
    }
    parseConversationData() {
        this.messages = this.conversation.messages;
        this.homeUser = this.conversation.getHomeUser();
        this.awayUser = this.conversation.getAwayUser();

    }



}



