import { Injectable, Inject } from "@angular/core";
import { Http, Response } from "@angular/http";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import { User } from "../model";
import { Headers, RequestOptions } from "@angular/http";
// import { APP_CONFIG, AppConfig } from "../app.config";
@Injectable()
export class MessageService {
  private clientsUrl = "";
  public headers = new Headers();
 public options = new RequestOptions({ headers: this.headers });

  constructor(private http: Http) {
    this.clientsUrl = "http://localhost:5000/api/Messages/";
  }
  create(user: User): Observable<User> {
    return this.http.post(this.clientsUrl, user, this.options).map(this.extractData).catch((err: any) => {
      return this.handleError(err);
    });
  }
  single(id: number): Observable<User> {
    return this.http.get(this.clientsUrl + id, this.options).map(this.extractData).catch((err: any) => {
      return this.handleError(err);
    });
  }
  update(id: number, user: User): Observable<User> {
    return this.http.put(this.clientsUrl + id, user, this.options).map(this.extractData).catch((err: any) => {
      return this.handleError(err);
    });
  }
  delete(id: number): Observable<User> {
    return this.http.delete(this.clientsUrl + id, this.options).map(this.extractData).catch((err: any) => {
      return this.handleError(err);
    });
  }

  getAll(): Observable<User[]> {
    return this.http.get(this.clientsUrl, this.options).map(this.extractData).catch((err: any) => {
      return this.handleError(err);
    });
  }
  private extractData(res: Response) {
    let body = res.json();

    return body;
  }
  private handleError(error: Response | any) {
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || "";
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ""} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.log(errMsg);
    return Observable.throw(errMsg);
  }
}



