import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { HomeComponent } from './components/home.component';
import { UsersComponent } from '../users/users.component';
import { MessagesComponent } from '../messages/messages.component';
import { HomeRoutes } from './home.routes';
import {FirstLetterPipe,LinkPreviewPipe} from '../shared/pipes';
import { LinkyModule } from 'angular-linky';
import { LinkPreviewComponent } from '../link-preview/link-preview.component';
import { AttachmentComponent } from '../attachment/attachment.component';
import {VgCoreModule} from 'videogular2/core';
import {VgControlsModule} from 'videogular2/controls';
import {VgOverlayPlayModule} from 'videogular2/overlay-play';
import {VgBufferingModule} from 'videogular2/buffering';  import 'hammerjs';
import { LightboxModule } from 'angular2-lightbox';

@NgModule({
    imports: [
        CommonModule,
        FormsModule,
        HttpClientModule,
        HomeRoutes,
        LinkyModule,
         VgCoreModule,
        VgControlsModule,
        VgOverlayPlayModule,
        VgBufferingModule,
        LightboxModule 

    ],

    declarations: [
        HomeComponent,
        UsersComponent,
        LinkPreviewComponent,AttachmentComponent,
        MessagesComponent,FirstLetterPipe,LinkPreviewPipe
    ],

    exports: [
        HomeComponent,
        UsersComponent
    ]
})

export class HomeModule { }



