import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { UserService, HubService} from './shared/services';
import {FirstLetterPipe} from './shared/pipes';

import { AppComponent } from './app.component';
import { AppRoutes } from './app.routes';
import { CoreModule } from './core/core.module';
import { HomeModule } from './home/home.module';
import { SharedModule } from './shared/shared.module';
import { APP_CONFIG, APP_DI_CONFIG } from "./shared/app.config";


@NgModule({
    imports: [
        BrowserModule,
        AppRoutes,
        SharedModule,
        CoreModule.forRoot(),
        HomeModule
        
       
    ],

    declarations: [
        AppComponent
    ],
     providers:[{ provide: APP_CONFIG, useValue: APP_DI_CONFIG },UserService,HubService],
    bootstrap: [AppComponent],
})

export class AppModule { }



