import { Component, OnInit,Input ,Output,EventEmitter,ViewEncapsulation} from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HubConnection } from '@aspnet/signalr-client';
import {HubService}  from '../shared/services'
import {User,Message,Conversation,CustomStatus}  from '../shared/model'

@Component({
    selector: 'app-users-component',
    templateUrl: './users.component.html',
    styleUrls:["./users.component.css"]
})

export class UsersComponent implements OnInit {
    private _hubConnection: HubConnection;
    @Input() conversations:Conversation[];
    mainUser:User;
    @Output() switchChat: EventEmitter<number> = new EventEmitter();
    currentConversationId:number;
    statusListVisible:boolean;
    constructor(private hubService:HubService) {

    }

   
    ngOnInit() {
         this.mainUser=this.conversations[0].getHomeUser();
   
}
changeStatus(status:CustomStatus)
{
   this.mainUser.customStatus=status;
    this.statusListVisible=false;
    this.hubService.invoke("NotifyStatusChange",this.mainUser.id,status );
}
customStatusClasses(cStatus:CustomStatus){
let cssClasses;
  
      switch(cStatus){
          case 0:  cssClasses = { 'online': true };
          break;
          case 1:  cssClasses = { 'offline': true };
           break;
          case 2:  cssClasses = { 'busy': true };
           break;
          case 3:  cssClasses = { 'away': true };
           break;
      }

  return cssClasses;
}
getStatusClasses(conversation:Conversation) {
  let cssClasses;
  let awayUser=conversation.getAwayUser(); 
  if(!awayUser.isOnline) {  
     cssClasses = {
       'offline': true
}
  } else {  
      cssClasses=this.customStatusClasses(awayUser.customStatus);
    	
  }
 
  return cssClasses;
}
showList(){
    this.statusListVisible=!this.statusListVisible;
}
isActive(conversationId)
{
    return this.currentConversationId==conversationId;
}
    switch(conversationId:number){
        this.currentConversationId=conversationId;
   this.switchChat.emit(conversationId);
    }
   
}



