import { Component, OnInit ,ViewEncapsulation} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { HubConnection } from '@aspnet/signalr-client';
import { UserService, HubService } from '../../shared/services'
import { User, TypingModel, Message, Conversation ,UserStatus} from '../../shared/model'

@Component({
  selector: 'app-home-component',
  templateUrl: './home.component.html',
  styleUrls: ["./home.component.css"]
})

export class HomeComponent implements OnInit {
  audio: any;
  currentUser: User;
  currentConversation: Conversation;
  conversations: Conversation[] = [];
  messages: Message[] = [];
  userId = 1;
  lostConnection:boolean;
  mainUser:User;
  constructor(private userservice: UserService, private hubService: HubService, private route: ActivatedRoute) {

    this.audio = new Audio("assets/audio/facebook.mp3");
    this.audio.load();
  }



  playAudio() {
    this.audio.play();
  }

  afterConnectionEstablished() {

    this.hubService.invoke('GetConversations', this.userId).then((conversations: Conversation[]) => {
          
      var allConversation:Conversation[]=[]
      for (var i = 0; i < conversations.length; i++) {
        let cnvs=new Conversation();
        cnvs.mapData(conversations[i],this.userId)
         allConversation.push(cnvs);
      }
     
      this.conversations.push(...allConversation);
       this.currentConversation=this.conversations[0];
       this.mainUser=this.conversations[0].getHomeUser();
    });

    this.hubService.sub("receiveMessage", (message: Message) => {
      if(message.userId!=this.mainUser.id)
      {
        this.playAudio();
      }
      var cnvs = this.conversations.filter(c => c.id == message.conversationId)[0];
      if (this.currentConversation.id != cnvs.id) {
        cnvs.hasUnreadMessages = true;
      }
      if (cnvs == null) {
        this.hubService.invoke('GetConversationById', message.conversationId).then((conversation: Conversation) => {
          conversation.homeUserId = this.userId;
          this.conversations.push(conversation);
         

        });
      }
      else {

        cnvs.messages.push(message);
      }


    });

    this.hubService.sub("statusChanged", (status: UserStatus) => {
        var cnvs=this.getConversationByUserId(status.userId);
        cnvs.getAwayUser().isOnline=status.connectedStatus;
        cnvs.getAwayUser().customStatus=status.customStatus;
    });
    this.hubService.onClose((e)=>{this.connectionLost()});

  this.hubService.sub("isTyping", (typingModel: TypingModel) => {
        var cnvs=this.getConversationById(typingModel.conversationId);
       cnvs.isTyping=typingModel.isTyping;
    });
  }
  
     getConversationById(id:number):Conversation{
  return this.conversations.filter(c => c.id==id)[0];
}
  connectionLost(){
    this.lostConnection=true;
  }
  reconnect(){
    this.hubService.initialize(this.userId).then(()=>{this.lostConnection=false;}).catch(()=>{alert("could not establish connection");});
  }
  getConversationByUserId(uid:number):Conversation{
  return this.conversations.filter(c => (c.homeUserId==c.user1Id)? c.user2Id==uid:c.user1Id==uid)[0];
}

  ngOnInit() {

    this.userId = this.route.snapshot.params['id'];
    this.hubService.initialize(this.userId).then(() => { this.afterConnectionEstablished(); });
  }

  switchChat(conversationId: number) {
    var existingConversation = this.conversations.filter(c => c.id == conversationId)[0];
    existingConversation.hasUnreadMessages = false;
    if (existingConversation == null) {
      this.hubService.invoke('GetConversationById', conversationId).then((conversation: Conversation) => {
        conversation.homeUserId = this.userId;
        this.conversations.push(conversation);
        this.currentConversation = conversation;

      });
    }
    else {
      this.currentConversation = existingConversation;
    }



  }


}



